import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule, Injectable, Injector } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { Pro } from '@ionic/pro';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Vibration } from '@ionic-native/vibration';
import { NativeAudio } from '@ionic-native/native-audio';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Shake } from '@ionic-native/shake';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { CreditsPage } from "../pages/credits/credits";
import { ListPage } from '../pages/list/list';

import { DiceRollerPageComponent } from '../pages/dice-roller-page/dice-roller-page';
import { AvatarRepositoryProvider } from '../providers/avatar-repository/avatar-repository';

Pro.init('7eded574', {
  appVersion: '0.0.1'
})

@Injectable()
export class MyErrorHandler implements ErrorHandler {
  ionicErrorHandler: IonicErrorHandler;

  constructor(injector: Injector) {
    try {
      this.ionicErrorHandler = injector.get(IonicErrorHandler);
    } catch(e) {
      // Unable to get the IonicErrorHandler provider, ensure
      // IonicErrorHandler has been added to the providers list below
    }
  }

  handleError(err: any): void {
    Pro.monitoring.handleNewError(err);
    // Remove this if you want to disable Ionic's auto exception handling
    // in development mode.
    this.ionicErrorHandler && this.ionicErrorHandler.handleError(err);
  }
}

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    CreditsPage,
    ListPage,
    DiceRollerPageComponent
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    CreditsPage,
    ListPage,
    DiceRollerPageComponent
  ],
  providers: [
    Shake,
    StatusBar,
    SplashScreen,
    Vibration,
    NativeAudio,
    IonicErrorHandler,
    [{ provide: ErrorHandler, useClass: MyErrorHandler }],
    InAppBrowser,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AvatarRepositoryProvider
  ]
})

export class AppModule {}



